<template>
  <div>
    <form @submit="register" class="container">
      <h1 class="h3 mb-3 fw-normal">Registrar restaurante</h1>

      <div class="form-floating">
        <input
          v-model="name"
          type="text"
          class="form-control"
          id="floatingInput"
          placeholder="Nombre"
          required
        />
        <label for="floatingInput">Nombre</label>
      </div>

      <div class="form-floating mt-2">
        <input
          v-model="city"
          type="text"
          class="form-control"
          id="floatingCity"
          placeholder="Ciudad"
          required
        />
        <label for="floatingCity">Ciudad</label>
      </div>

      <div class="form-floating mt-2">
        <input
          v-model="direction"
          type="text"
          class="form-control"
          id="floatingDirection"
          placeholder="Direccion"
          required
        />
        <label for="floatingDirection">Direccion</label>
      </div>

      <div class="form-floating mt-2">
        <textarea
          v-model="description"
          type=""
          class="form-control"
          id="floatingDescription"
          placeholder="Descripción"
          required
        />
        <label for="floatingDescription">Descripción</label>
      </div>

      <div class="form-floating mt-2">
        <input
          v-model="photo_url"
          type="text"
          class="form-control"
          id="floatingDirection"
          placeholder="Enlace de la foto"
          required
        />
        <label for="floatingDirection">Enlace de la foto</label>
      </div>

      <button class="w-100 btn btn-lg btn-primary mt-2" type="submit">
        Registrar
      </button>
    </form>
  </div>
</template>

<script>
export default {
  name: "RegistrarRestaurante",
  data() {
    return {
      name: "",
      city: "",
      direction: "",
      description: "",
      photo_url: ""
    };
  },
  methods: {
    async register(e) {
      e.preventDefault();
      const response = await fetch("https://api-restaurantes-test.herokuapp.com/api/restaurant/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          name: this.name,
          city: this.city,
          direction: this.direction,
          description: this.description,
          url_photo: this.photo_url
        })
      });

      const data = await response.json();

      if (data.error === null) {
        return alert("Restaurante registrado con éxito");
      }

      alert(data.error);
    }
  }
};
</script>
