<template>
  <div>
    <div v-for="reservation in reservations" :key="reservation.id">
      <div class="card mx-auto my-3" style="width: 30%">
        <img :src="reservation.url_photo" class="card-img-top" alt="..." />
        <div class="card-body">
          <h5 class="card-title">
            Reservación para: {{ reservation.reserved_by }}
          </h5>
          <h6>Restaurante: {{ reservation.name }}</h6>
          <p class="card-text">Fecha: {{ reservation.date }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ListarReservas",
  data() {
    return {
      reservations: []
    };
  },
  methods: {
    async fetchReservations() {
      const res = await fetch("https://api-restaurantes-test.herokuapp.com/api/reservation/");
      const data = await res.json();

      console.log(data);
      if (data.error !== null) {
        console.log("An error has ocurred");
        return;
      }

      return data.data;
    }
  },
  async created() {
    this.reservations = await this.fetchReservations();
    console.log(this.reservations);
  }
};
</script>
