<template>
  <div>
    <form @submit="register" class="container">
      <h1 class="h3 mb-3 fw-normal">Registrar reservación</h1>

      <div class="form-floating">
        <input
          v-model="name"
          type="text"
          class="form-control"
          id="floatingInput"
          placeholder="Nombre"
        />
        <label for="floatingInput">Nombre</label>
      </div>

      <div class="form-floating mt-2">
        <input
          v-model="date"
          type="date"
          class="form-control"
          id="floatingCity"
          placeholder="Ciudad"
        />
        <label for="floatingCity">Fecha</label>
      </div>

      <div class="form-floating mt-2">
        <select class="p-3 form-control" v-model="restaurantName">
          <option selected disabled="disabled"
            >Seleccione un restaurante</option
          >
          <option
            v-for="restaurant in restaurants"
            :key="restaurant.id"
            selected
          >
            {{ restaurant.id }} - {{ restaurant.name }}
          </option>
        </select>
      </div>

      <button class="w-100 btn btn-lg btn-primary mt-2" type="submit">
        Registrar
      </button>
    </form>
  </div>
</template>

<script>
export default {
  name: "RegistrarReservacion",
  data() {
    return {
      name: "",
      date: "",
      restaurants: [],
      restaurantName: ""
    };
  },
  methods: {
    async register(e) {
      e.preventDefault();
      const restaurant_id = this.restaurantName.split(" ")[0];

      const response = await fetch("https://api-restaurantes-test.herokuapp.com/api/reservation/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          reserved_by: this.name,
          date: this.date,
          restaurant_id
        })
      });

      console.log(response);

      const data = await response.json();

      console.log("aca");
      console.log(data);
      if (data.error === null) {
        return alert("Reservación registrada con éxito!");
      }

      alert(data.error);
    },
    async fetchRestaurants() {
      const response = await fetch("https://api-restaurantes-test.herokuapp.com/api/restaurant/");
      const data = await response.json();
      return data.data;
    }
  },
  async created() {
    this.restaurants = await this.fetchRestaurants();
  }
};
</script>
